import React from "react";
import { useParams } from "react-router-dom";
import { Nav } from "react-bootstrap";
import { useState, useEffect } from "react"; //   
import { addItem } from "../store.js";
import { useDispatch } from "react-redux";
import { Button } from "react-bootstrap";
import { Link } from "react-router-dom";

import styled from "styled-components";

let Banner = styled.div`
  padding: 20px;
  color: gray;
`;

let BannerBtn = styled.button`
  color: white;
  font-size: 30px;
  width: 100%;
  padding: 100px 100px;
  border: 1px solid #ccc;
  background-image: url("../img/banner.jpg");
  background-size: cover;
  background-position: center;
`;

function Detail(props) {
  let { paramId } = useParams(); // paramId      .
  let [tap, setTap] = useState(0);

  // React Hook      
  let [fade2, setFade2] = useState("");

  // dispatch  
  const dispatch = useDispatch();

  useEffect(() => {
    setFade2("end");
    return () => {
      setFade2("");
    };
  }, []);

  //    ( Hook    )

  let selproduct = props.fruit.find((x) => x.id === Number(paramId));

  //  (if)     (React Hook   )
  if (!selproduct) {
    return <div>   .</div>;
  }

  const { id, imgUrl, title, content, price } = selproduct;

  console.log("  : " + id + " " + title);

  return (
    <div className={"container start " + fade2}>
      <Banner>
        <BannerBtn>   .</BannerBtn>
      </Banner>
      <div className="row">
        <div className="col-md-6">
          <img src={"/" + imgUrl} width="100%" alt={title} />
        </div>
        <div className="col-md-6">
          <h4 className="pt-5">{title}</h4>
          <p>{content}</p>
          <p>{price}</p>
          <Button
            variant="primary"
            onClick={() => {
              //  dispatch(addItem(  {id : 2,  imgurl : 'shoes1.jpg', name : 'Grey Yordan', count : 1}))

              dispatch(
                addItem({
                  id: id,
                  imgurl: imgUrl.replace("img/", ""),
                  name: title,
                  count: 1,
                })
              );
            }}
            style={{ marginRight: "10px" }}
          >
            
          </Button>

          <Link to="/cart">
            <Button variant="outline-success">   </Button>
          </Link>
        </div>
      </div>

      <Nav
        variant="tabs"
        defaultActiveKey="link0"
        style={{ marginTop: "50px" }}
      >
        <Nav.Item>
          <Nav.Link
            onClick={() => {
              setTap(0);
            }}
            eventKey="link0"
          >
            0
          </Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link
            onClick={() => {
              setTap(1);
            }}
            eventKey="link1"
          >
            1
          </Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link
            onClick={() => {
              setTap(2);
            }}
            eventKey="link2"
          >
            2
          </Nav.Link>
        </Nav.Item>
      </Nav>

      <TabContent tap={tap} />
    </div>
  );
}

function TabContent({ tap }) {
  let [fade, setFade] = useState("");
  useEffect(() => {
    setTimeout(() => {
      setFade("end");
    }, 10);
    return () => {
      setFade("");
    };
  }, [tap]);

  return (
    <div className={"start " + fade}>
      {[<div>0</div>, <div>1</div>, <div>2</div>][tap]}
    </div>
  );
}

export default Detail;
